import React from 'react';
import Jumbotron from 'react-bootstrap/Jumbotron';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Link } from 'react-router-dom';

function Hero(props) {

    return(
        <Jumbotron className="bg-transparent jumbotron-fluid p-0">
            <Container fluid={true}>
                <Row className="justify-content-center py-5">
                    <Col id="home" md={8} sm={12}>
                        { props.title && <h1 id="hello" className="display1">{props.title}</h1> }
                        { props.title && <h1 className="display1">{props.underTitle}</h1> }
                        { props.subTitle && <h3 className="display4">{props.subTitle}</h3> }
                        <Link className ="link" to="/projects">{ props.text && <h3 id="linkBox" className="lead font-weight-light">{props.text}</h3> }</Link>
                    </Col>
                </Row>
            </Container>
        </Jumbotron>
    );

}


export default Hero;