import React from 'react';
import Hero from '../components/Hero';
import Content from '../components/Content';

function AboutPage(props) {

    return(
        <div>
            

            <Content>
            <br></br>
            <br></br>
            <br></br>
            <div className="para">
                <h1 id="hello" className="display1">{props.title}</h1>
                <h3 className="display4">A little about why I chose software engineering</h3> 
                <br/>
                <p>Ever rouse awake at night unimaginably thirsty and then proceed to have the best cup of water in your whole life? That's what programming was like for me when I was able to occasionally dabble into it, at Uni, at work and even at home.</p>

                <p>I knew something was up when, as a consultant, I always looked forward to the, so-called, "chore" of going through the clients’ code bases. I'd go in guns blazing, trying to poke holes in their logic, but always left humbled and impressed at their solutions. At school it started as a way to fill my credits but soon became my favorite courses. </p>

                <p>I realized that I was thirsty. All these little "Aha!" moments got louder and louder until I couldn't ignore it anymore and I listened to my heart and completed a coding bootcamp. Juggling both a full-time job and a bootcamp was a "Masterclass" in time-management, determination and communication. I refused to compromise on quality on both fronts and was so glad I went for it.</p>

                <p>It was so much fun! Being able to understand things like resource costs and time complexity helped me understand why some clients used the Luhn algorithm to generate a “checksum. It was not only faster than constantly checking against their massive database, but it also cost less resources. It was like I was finally waking up to the world around me and is still such a great feeling.</p>

                <p>Programming is where my heart is, I’m awake now and I'm still thirsty for more!</p>
            </div> 
            </Content>
        </div>
    );

}

export default AboutPage;