import React from 'react';
import {BrowserRouter as Router, Route, Link } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import { Document, Page, pdfjs } from "react-pdf";

import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import './App.css';

import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import ContactPage from './pages/ContactPage';
import ProjectPage from './pages/ProjectsPage';
// import resume from '%PUBLIC_URL%/Alexander_Zhao_2020_Resume.pdf'



class App extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      title: 'Alexander Zhao',
      headerLinks: [
        { title: 'Home', path: '/' },
        { title: 'About', path: '/about' },
        { title: 'Contact', path: '/contact' },
        { title: 'Projects', path: '/projects' }
      ],
      home: {
        title: 'Hello,',
        underTitle: 'I\'m Alexander Zhao.' ,
        subTitle: 'I\'m a software engineer with a background in data transfer technologies',
        text: 'Checkout my projects >>>'
      },
      about: {
        title: 'About Me'
      },
      contact: {
        title: 'Let\'s Talk'
      },
      projects: {
        title: 'Check it out'
      }
    }
  }

  componentDidMount(){
    pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`
  }

  render() {
    pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`
    return (
      <Router>
        <Container className="p-0" fluid={true}>
          
          <Navbar bg="transparent" expand="lg">

            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle" />
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/">Home</Link>
                <Link className="nav-link" to="/resume">Resume</Link>
                <Link className="nav-link" to="/projects">Projects</Link>
                <Link className="nav-link" to="/about">About</Link>
                <Link className="nav-link" to="/contact">Contact</Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
          <Route exact path="/alexzhao"  render={() => <HomePage underTitle={this.state.home.underTitle} title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />} />
          <Route exact path="/about" render={() => <AboutPage title={this.state.about.title} />} />
          <Route exact path="/contact" render={() => <ContactPage title={this.state.contact.title} />} />
          <Route exact path="/projects" render={() => <ProjectPage title={this.state.projects.title} />} />
          <Route exact path="/resume" render={() => <Document file="https://azhao8922.github.io/alexzhao/Alexander_Zhao_2020_Resume.pdf"><Page scale="2" pageNumber={1} /></Document> } />
          <Route exact path="/" render={() => <HomePage underTitle={this.state.home.underTitle} title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />} />
        </Container>
      </Router>
    );
  }
}

export default App;