import React from 'react';
import { useSpring, animated } from 'react-spring';

function CardInfo(props) {

    const style = useSpring({opacity: 1, from: {opacity: 0}});

    return(
        <animated.div className="g-card-info" style={style}>
            <p className="g-card-title">{props.title}</p>
            <p className="g-card-sub-title">{props.subTitle}</p>
            <p className="g-card-sub-title">{props.stack}</p>
            <a href={props.link} target="_blank" rel="noopener noreferrer">Github Repo | </a>
            {props.video !== '' ? <a href={props.video} target="_blank" rel="noopener noreferrer">Video Demo | </a>:''}
            <a href={props.deployed} target="_blank" rel="noopener noreferrer">Deployed App</a>
        </animated.div>
    );

}

export default CardInfo;