import React from 'react';

import CardInfo from '../components/CardInfo';


function Card(props) {
    let {selected, title, subTitle, imgSrc, stack, link, video, deployed} = props.item
    return(
        <div className="d-inline-block g-card" onClick={(e) => props.click(props.item)}>
            <img className="g-card-image" src={imgSrc} alt={imgSrc} />
            { selected && <CardInfo title={title} subTitle={subTitle} link={link} video={video} deployed={deployed} stack={stack} /> }
        </div>
    );

}

export default Card;